import { ErrorHandler, NgModule } from '@angular/core';
import { DatePipe } from './date.pipe';
import { NumberPipe } from './number.pipe';
import { IntlService } from './intl.service';
import { CldrIntlService } from './cldr-intl.service';
import { IntlErrorHandler } from './intl-error-handler';

const pipes = [
    DatePipe,
    NumberPipe
];
/**
 * Represents the [NgModule](https://angular.io/docs/ts/latest/guide/ngmodule.html)
 * definition for the Intl services.
 */
@NgModule({
    declarations: [pipes],
    exports: [pipes],
    providers: [
        { provide: IntlService, useClass: CldrIntlService },
        { provide: ErrorHandler, useClass: IntlErrorHandler }
    ]
})
export class IntlModule {}



